import { DefaultTheme } from "styled-components";

export const lightTheme: DefaultTheme={
    isDark:false,
    bgColor:"white",
    textColor:"black",
    accentColor: "#9c88ff",
    borderColor: "black",
};

export const darkTheme: DefaultTheme={
    isDark:true,
    bgColor: "#2f3640",
    textColor: "#f5f6fa",
    accentColor: "#9c88ff",
    borderColor: "#2f3640",
};


