import { BrowserRouter, Switch, Route, Link } from "react-router-dom";
import styled from "styled-components";
import Coin from "./routes/Coin";
import Coins from "./routes/Coins";

interface IRouter{
  isDark:boolean;
  onChangeModeClick(): void;
}

function Router({isDark, onChangeModeClick} : IRouter) {

  const MenuTools = styled.div`
    padding: 0px 20px;
    max-width: 480px;
    margin: 0 auto;
    display:flex;
    
  `;

  const HomeButton=styled.button`
    font-size : 20px;
    border-radius : 10px;
    background-color : ${props=>props.theme.bgColor};
    color:${props=>props.theme.textColor};
    margin-left:auto;
  `;
  
  return (
    <BrowserRouter>
      <MenuTools>
          <Link to="/">
             <HomeButton>Home</HomeButton>
          </Link>
          <HomeButton onClick={onChangeModeClick}>{isDark ? "light Mode" : "dark Mode"}</HomeButton>
       </MenuTools>
      <Switch>
        <Route path="/:coinId">
          <Coin isDark={isDark}/>
        </Route>
        <Route path="/">
          <Coins isDark={isDark} />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}
export default Router;
